#experience{
    padding: 8px;
}
.content{
    width: 100%;
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
    text-align: center;
    box-sizing: border-box;
}
.skill-{
    min-width: 25%;
    min-height: 150px;
    padding: 15px;
    align-items: center;
    color: rgb(88, 84, 84);
    
    
}
.skill- img{
    padding-bottom: 11px;
    padding: 10px;
    max-width: 5rem;
}
@media screen  and (max-width:464px){
    .skill-{
        /* width: 100%; */
        width: 50%;
        padding: 0;
    }
    .skill- h5 { 
        font-size: 1.2rem;
    }
    .skill- img{
        max-width: 5.5rem;
        padding: 2px;
    }
    
}